.popup {
  visibility: hidden;
  opacity: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  background: rgba(0, 0, 0, 0.5);
  transition: visibility .3s linear, opacity .3s linear;
  /* для возвышения над мусоркой */
  z-index: 3;
}